<template>
	<view class="">
		<view class="padl20 mart10 marb10">订单号：</view>
		<view class="padl20 mart10 marb10">品类：</view>
		<view class="padl20 mart10 marb10">数量：</view>
		<view class="padl20 mart10 marb10">洗衣进度</view>
		<view class="scrollable marl133">
			<uni-steps
				:options="[{title:'过水',desc:'2018-11-11'},{title:'清洗',desc:'2018-11-12'},{title:'消毒',desc:'2018-11-13'},{title:'烘干',desc:'2018-11-14'}]"
				direction="column" :active="2"></uni-steps>
		</view>
	</view>
</template>

<script>
	import {
		HuiShouJiLu
	} from '@/public/qj_request.js'
	export default {
		data() {
			return {
				saomiao: false, //false显示开始
				templedata: '',
				list: [],
			}
		},
		onLoad() {},
		methods: {
			gogetstatistics() { //点击下一步
				var that = this
				if (that.saomiao == false) { //检测未关闭扫描状态false为关闭
					var data = {
						operator: that.name,
						recycleList: that.list
					}
					console.log('传过去的', data);
					HuiShouJiLu(data).then(res => {
						console.log('回收记录', res);
						// uni.navigateTo({
						// 	url: "/pages/getStatistics/getStatistics"
						// })
					})
				} else {
					uni.showToast({
						title: '未关闭扫描',
						icon: 'none',
						duration: 2000 //持续的时间
					})
				}
			},
			/* inventoryStart */
			inventoryStart() { //开启扫描
				var that = this
				that.saomiao = !that.saomiao
				console.log('扫描');
				andoridModule.inventoryStart((e) => {
					console.log('扫描', e);
					if (e != 'false') {
						// var sao = JSON.stringify(e)
						// var saomiao = JSON.parse(sao)
						console.log("扫描出：", e);
						var newArray = that.list
						e.forEach(newData => {
							that.insertData(newArray, newData);
						});
						for (var i = 0; i < e.length; i++) {
							that.playsoundPool();
							console.log(that.list);
						}
					} else {
						that.templedata = e
					}
				});
			},

			insertData(existingData, newItem) { //插入新数组并计数
				let found = false;
				for (let item of existingData) {
					if (item.id === newItem.id) {
						item.count++;
						found = true;
						break;
					}
				}
				if (!found) {
					existingData.push(newItem);
				}
			},
			playsoundPool: function() { //声音
				var that = this
				andoridModule.playSound((e) => {
					this.templedata = e
					// console.log("playsoundPool：" + e);
				});
			},

			inventoryStop() { //关闭扫描
				var that = this
				that.saomiao = !that.saomiao
				andoridModule.inventoryStop((e) => {
					this.templedata = e
					console.info("inventoryStop关闭：" + JSON.stringify(e))
				});
			},
			qing() { //清空列表
				var that = this
				that.list = []
			}
		}
	}
</script>

